
import { defineComponent } from 'vue'
import { runASWEvent as save } from '@/function/option'
import { runtimeData } from '@/function/msg'

export default defineComponent({
  name: 'ViewOptFunction',
  data () {
    return {
        runtimeData: runtimeData,
      save: save,
      ndt: 0,
      ndv: false
    }
  },
  methods: {
    msgND: function () {
      this.ndt++
      setTimeout(() => {
        this.ndv = false
      }, 300)
    }
  }
})
